/**
 *  - API
 */

import axios from 'axios';

// 
export const testApiConnectivity = async (ports = [8085, 8086]) => {
    console.log(' API...');
    const results = [];

    for (const port of ports) {
        try {
            console.log(` ${port}...`);
            const testUrl = `http://localhost:${port}/api/ai/test`;

            const response = await axios.get(testUrl, {
                timeout: 3000
            });

            results.push({
                port,
                success: true,
                status: response.status,
                data: response.data
            });

            console.log(`  ${port} `, response.data);
        } catch (error) {
            results.push({
                port,
                success: false,
                error: error.message,
                details: error.response ? {
                    status: error.response.status,
                    data: error.response.data
                } : 'No response'
            });

            console.log(`  ${port} :`, error.message);
        }
    }

    console.log(' API:', results);
    return results;
};

// 
export const showDebugInfo = (apiConfig) => {
    const debugInfo = {
        version: '1.0.0',
        timestamp: new Date().toISOString(),
        api: apiConfig,
        browser: {
            userAgent: navigator.userAgent,
            language: navigator.language,
            platform: navigator.platform
        }
    };

    console.log(' :', debugInfo);
    return debugInfo;
};

// Vue
export const setupDebugHelpers = (vueComponent) => {
    // window
    window.$debug = {
        testApiConnectivity,
        showDebugInfo,
        api: vueComponent.$data.backend,
        reload: () => {
            vueComponent.testConnection();
            return '...';
        }
    };

    console.log('  $debug ');
}; 